<template id="SttingScreen">
  <div>
    <div>
      <b-card id="settingsCard" class="borderAlign" no-body>
        <b-tabs v-model="tabIndex">
          <b-tab
            :title-link-class="linkClass(0)"
            :title="$t('BASICINFORMATION')"
            class="tabContent"
          >
            <b-form class="mobileTabContent">
              <b-row>
                <b-col cols="12" xl="6" lg="6">
                  <b-col cols="12" class="mt-1">
                    <b-button
                      variant="primary"
                      class="px-4 required-btnAny align-content-center"
                      >{{ $t("REQUIREDBUTTON") }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel">{{
                      $t("COMPANYNAMEORNAME")
                    }}</label>
                  </b-col>
                  <b-col cols="12" class="mt-2">
                    <b-form-input
                      v-model.trim="$v.basicSettingInfo.companyName.$model"
                      maxlength="100"
                      class="textFieldClass"
                    ></b-form-input>
                    <show-errors
                      :control="$v.basicSettingInfo.companyName"
                      :field="$t('COMPANYNAMEORNAME')"
                    />
                  </b-col>
                </b-col>
                <b-col cols="12" xl="6" lg="6" class="mobileAlignMent">
                  <b-col cols="12" class="mt-1">
                    <b-button
                      variant="primary"
                      class="px-4 required-btnAny align-content-center"
                      >{{ $t("REQUIREDBUTTON") }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel">{{
                      $t("PERSONINCHARGE")
                    }}</label>
                  </b-col>
                  <b-col cols="12" class="mt-2">
                    <b-form-input
                      v-model="$v.basicSettingInfo.personInCharge.$model"
                      maxlength="50"
                      class="textFieldClass"
                    ></b-form-input>
                    <show-errors
                      :control="$v.basicSettingInfo.personInCharge"
                      :field="$t('PERSONINCHARGE')"
                    />
                  </b-col>
                </b-col>
              </b-row>
              <br />
              <b-row>
                <b-col cols="12" xl="6" lg="6">
                  <b-col cols="12" class="mt-1">
                    <b-button
                      variant="primary"
                      class="px-4 required-btnAny align-content-center"
                      >{{ $t("REQUIREDBUTTON") }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel">{{
                      $t("PHONENO")
                    }}</label>
                  </b-col>
                  <b-col cols="12" class="mt-2">
                    <b-form-input
                      v-model.trim="$v.basicSettingInfo.phoneNo.$model"
                      maxlength="11"
                      class="textFieldClass"
                      onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                    ></b-form-input>
                    <show-errors
                      :control="$v.basicSettingInfo.phoneNo"
                      :field="$t('PHONENO')"
                    />
                  </b-col>
                </b-col>
                <b-col cols="12" xl="6" lg="6" class="mobileAlignMent">
                  <b-col cols="12" class="mt-1">
                    <b-button
                      variant="primary"
                      class="px-4 required-btnAny align-content-center"
                      >{{ $t("REQUIREDBUTTON") }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel">{{
                      $t("MAILADDRESS")
                    }}</label>
                  </b-col>
                  <b-col cols="12" class="mt-2">
                    <b-form-input
                      v-model.trim="$v.basicSettingInfo.email.$model"
                      maxlength="254"
                      class="textFieldClass"
                    ></b-form-input>
                    <show-errors
                      :control="$v.basicSettingInfo.email"
                      :field="$t('MAILADDRESS')"
                    />
                  </b-col>
                </b-col>
              </b-row>
              <br />
              <b-row>
                <b-col cols="12">
                  <b-col cols="12" class="mt-1">
                    <b-button
                      variant="primary"
                      class="px-4 required-btnAny align-content-center"
                      >{{ $t("REQUIREDBUTTON") }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel">{{
                      $t("Headquarterslocation")
                    }}</label>
                  </b-col>
                  <b-col id="options" cols="12" xl="2" lg="2" class="mt-2">
                    <b-form-select
                      v-model="selectedOffice"
                      :options="officeOptions"
                      value-field="item"
                      text-field="name"
                      disabled-field="notEnabled"
                      class="optionClass dropDownButton"
                    >
                    </b-form-select>
                  </b-col>
                </b-col>
              </b-row>
              <br />
              <b-row>
                <b-col cols="12">
                  <b-col cols="12">
                    <b-form-input
                      v-model.trim="$v.basicSettingInfo.officeAddress.$model"
                      :placeholder="$t('HEADQUARTERSCITY')"
                      maxlength="300"
                      class="textFieldClass"
                    ></b-form-input>
                    <show-errors
                      :control="$v.basicSettingInfo.officeAddress"
                      :field="$t('HeadquartersAddress')"
                    />
                  </b-col>
                </b-col>
              </b-row>

              <br />
              <br />
              <b-row>
                <b-col cols="12" xl="12" lg="12" class="text-center">
                  <b-button
                    @click="clientBasicInfoCheck"
                    variant="primary"
                    class="px-4 align-content-center"
                    >{{ $t("UPDATE") }}</b-button
                  >
                </b-col>
              </b-row>
            </b-form>
          </b-tab>

          <b-tab :title-link-class="linkClass(1)" :title="$t('ENTERPRISESIZE')">
            <b-form class="mobileTabContent">
              <b-row>
                <b-col cols="12" xl="5" lg="5" class="ml-3 mobileIndustry">
                  <label id="Label"
                    >{{ tableYear }}{{ $t("IndustryType")
                    }}{{ companySizeName }}</label
                  >
                </b-col>
              </b-row>
              <b-row class="no-gutters">
                <b-col cols="12" class="ml-3">
                  <b-button
                    variant="primary"
                    class="px-4 required-btnAny align-content-center"
                    >{{ $t("REQUIREDBUTTON") }}</b-button
                  >&nbsp;&nbsp;
                  <label class="highLightHeaderLabel">{{
                    $t("Industry")
                  }}</label>
                </b-col>
                <b-col cols="12" xl="4" lg="4">
                  <b-col id="industryDropDown" cols="12" xl="12" lg="12">
                    <b-form-select
                      v-model="companySettingInfo.selectedIndustry"
                      :options="industryOptions"
                      value-field="value"
                      text-field="text"
                      disabled-field="disabled"
                      class="optionClass dropDownButton"
                    ></b-form-select>
                    <show-errors
                      :control="$v.companySettingInfo.selectedIndustry"
                      :field="$t('Industry')"
                    />
                  </b-col>
                </b-col>
                <b-col cols="12" xl="2" lg="2">
                  <b-col
                    cols="12"
                    xl="12"
                    lg="12"
                    class="float-right mobileJustify"
                  >
                    <b-button
                      :disabled="isRowSelected"
                      @click="createCompanyInfo"
                      class="btnWidth float-right"
                      variant="primary"
                      >{{ $t("ADD") }}
                      <i v-if="addBusy" class="fa fa-spinner fa-spin" />
                    </b-button>
                  </b-col>
                </b-col>
              </b-row>
              <b-row class="mt-2">
                <b-col cols="12" xl="6" lg="6">
                  <b-col cols="12" class="mt-2">
                    <b-button
                      variant="primary"
                      class="px-4 required-btnAny align-content-center"
                      >{{ $t("REQUIREDBUTTON") }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel">{{
                      $t("YEAR1")
                    }}</label>
                  </b-col>
                  <b-col id="options" cols="12" class="mt-2">
                    <b-form-select
                      v-model="selectedYear"
                      :options="options"
                      :disabled="onRowDisabled"
                      value-field="item"
                      text-field="name"
                      class="optionClass dropDownButton"
                      disabled-field="notEnabled"
                    ></b-form-select>
                  </b-col>
                </b-col>
                <b-col cols="12" xl="6" lg="6" class="mobileAlignMent">
                  <b-col cols="12" class="mt-2">
                    <b-button variant="primary" class="px-4 required-btnAny">{{
                      $t("REQUIREDBUTTON")
                    }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel">{{
                      $t("EmployeeNumber")
                    }}</label>
                  </b-col>
                  <b-col cols="12" class="mt-2">
                    <b-form-input
                      v-model.trim="companySettingInfo.employee"
                      :placeholder="'1,000' + $t('PEOPLE')"
                      maxlength="7"
                      class="textFieldClass"
                      onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                    ></b-form-input>
                    <show-errors
                      :control="$v.companySettingInfo.employee"
                      :field="$t('EmployeeNumber')"
                    />
                  </b-col>
                </b-col>
              </b-row>
              <br />
              <b-row>
                <b-col cols="12" xl="6" lg="6" class="mobileAlignTop">
                  <b-col cols="12" class="mt-2">
                    <b-button variant="primary" class="px-4 required-btnAny">{{
                      $t("REQUIREDBUTTON")
                    }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel"
                      >{{ $t("Capital") }}
                    </label>
                  </b-col>
                  <b-col cols="12" class="mt-2">
                    <b-form-input
                      v-model.trim="companySettingInfo.capital"
                      :placeholder="'5,000,000' + $t('YEN')"
                      maxlength="14"
                      class="textFieldClass"
                      onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                    ></b-form-input>
                    <show-errors
                      :control="$v.companySettingInfo.capital"
                      :field="$t('Capital')"
                    />
                  </b-col>
                </b-col>
                <b-col cols="12" xl="6" lg="6" class="mobileAlignMent">
                  <b-col cols="12" class="mt-2">
                    <b-button variant="primary" class="px-4 required-btnAny">{{
                      $t("REQUIREDBUTTON")
                    }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel">{{
                      $t("Revenue")
                    }}</label>
                  </b-col>
                  <b-col cols="12" class="mt-2">
                    <b-form-input
                      v-model.trim="companySettingInfo.sales"
                      :placeholder="'10,000,000' + $t('YEN')"
                      maxlength="14"
                      class="textFieldClass"
                      onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                    ></b-form-input>
                    <show-errors
                      :control="$v.companySettingInfo.sales"
                      :field="$t('Revenue')"
                    />
                  </b-col>
                </b-col>
              </b-row>
              <br />
              <b-row>
                <b-col cols="12" xl="6" lg="6">
                  <b-col cols="12">
                    <b-button variant="primary" class="px-4 required-btnAny">{{
                      $t("REQUIREDBUTTON")
                    }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel">{{
                      $t("FloorSpace")
                    }}</label>
                  </b-col>
                  <b-col cols="12" class="mt-2">
                    <b-form-input
                      v-model.trim="companySettingInfo.floorSize"
                      :placeholder="'100' + $t('PING')"
                      maxlength="9"
                      class="textFieldClass"
                      onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                    ></b-form-input>
                    <show-errors
                      :control="$v.companySettingInfo.floorSize"
                      :field="$t('FloorSpace')"
                    />
                  </b-col>
                </b-col>
                <b-col cols="12" xl="6" lg="6" class="mobileAlignMent">
                  <b-col cols="12">
                    <b-button variant="primary" class="px-4 required-btnAny">{{
                      $t("REQUIREDBUTTON")
                    }}</b-button
                    >&nbsp;&nbsp;
                    <label class="highLightHeaderLabel">{{
                      $t("NumberOfStores")
                    }}</label>
                  </b-col>
                  <b-col cols="12" class="mt-2">
                    <b-form-input
                      v-model.trim="companySettingInfo.storeNo"
                      :placeholder="'50' + $t('STOREUNIT')"                      
                      maxlength="7"
                      class="textFieldClass"
                      onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                    ></b-form-input>
                    <show-errors
                      :control="$v.companySettingInfo.storeNo"
                      :field="$t('NumberOfStores')"
                    />
                  </b-col>
                </b-col>
              </b-row>
              <br />
              <b-row class="mt-1">
                <b-col cols="6" class="text-right">
                  <b-button
                    :disabled="isAddSelected"
                    @click="updateCompanyInfo"
                    variant="primary"
                    class="btnWidth"
                    >{{ $t("UPDATE") }}
                    <i v-if="updBusy" class="fa fa-spinner fa-spin" />
                  </b-button>
                </b-col>
                <b-col cols="6" class="text-left">
                  <b-button
                    @click="clearbasicSettingInfo"
                    variant="primary"
                    class="btnWidth disableBtn"
                    >{{ $t("CLEAR") }}</b-button
                  >
                </b-col>
              </b-row>
              <br />
              <b-card id="settingCard" no-body >
                <div
                  id="settingTable"
                  class="ml-4 mr-1 scroller scrollbar-width-thin scrollbar-color"
                >
                  <b-table
                    id="selectableTable"
                    ref="selectableTable"
                    :busy="isBusy"
                    :fields="fieldss"
                    :items="tableData"
                    :striped="true"
                    :bordered="false"
                    :borderless="false"
                    :outlined="false"
                    head-variant="dark"
                    fixed
                    sticky-header
                  >
                    <template v-slot:cell(selectRow)="item">
                      <template>
                        <b-form-radio
                          :id="'radio-' + item.index"
                          v-model="item.item.selectRow"
                          :name="'table-radio'"
                          @change="toggleSelectAll(item)"
                        ></b-form-radio>
                      </template>
                    </template>
                    <template v-slot:cell(Year)="item">{{
                      item.value
                    }}</template>
                    <template v-slot:cell(EmployeeNumber)="item">{{
                      item.value
                    }}</template>
                    <template v-slot:cell(Capital)="item">{{
                      item.value
                    }}</template>
                    <template v-slot:cell(Revenue1)="item">{{
                      item.value
                    }}</template>
                    <template v-slot:cell(Floorspace)="item">{{
                      item.value
                    }}</template>
                    <template v-slot:cell(NumberOfStores)="item">{{
                      item.value
                    }}</template>
                    <template v-slot:cell(IndustryType)="item">{{
                      item.value
                    }}</template>
                    <template v-slot:table-busy>
                      <div class="text-center text-danger my-2">
                        <b-spinner class="align-middle"></b-spinner>
                      </div>
                    </template>
                  </b-table>
                  <div class="text-center text-danger mb-2 mt-2">
                    {{ errorMessage }}
                  </div>
                </div>
              </b-card>
            </b-form>
          </b-tab>
          <b-tab :title-link-class="linkClass(2)" :title="$t('OTHER')">
            <b-form id="tab3" class="tab3MobileTabContent">
              <br />
              <b-row class="no-gutters">
                <b-col cols="12">
                  <b-button
                    variant="outline-primary"
                    class="px-4 required-btnAny1"
                    >{{ $t("Any") }}</b-button
                  >&nbsp;&nbsp;
                  <label class="highLightHeaderLabel">{{
                    $t("StartMonth")
                  }}</label>
                </b-col>
                <b-col id="monthSelect" cols="10" xl="5" lg="5" class="mt-3">
                  <b-form-select
                    @change="onChange($event)"
                    v-model="selectedMonth"
                    :options="monthOptions"
                    value-field="item"
                    text-field="name"
                    class="optionClass dropDownButton"
                    disabled-field="notEnabled"
                  ></b-form-select>
                </b-col>
              </b-row>
              <br />
              <b-row class="">
                <b-col cols="12">
                  <b-button
                    variant="outline-primary"
                    class="px-4 required-btnAny1"
                    >{{ $t("Any") }}</b-button
                  >&nbsp;&nbsp;
                  <label class="highLightHeaderLabel">{{
                    $t("ComPanyIcon")
                  }}</label>
                </b-col>
                <b-col cols="12" xl="1" lg="1" class="justifyMobile">
                  <div id="thumbnailLogoClient" v-if="imagePath">
                    <img
                      @click="onPickFile()"                    
                      v-if="imagePath"
                      v-bind:src="baseDOMAIN + imagePath"
                      class="thumbnailLogoImage"
                      alt="client_icon"
                    />
                    <input
                      id="fileupload"
                      ref="fileupload"
                      @change="onFileChange"
                      type="file"
                      name="fileupload"
                      hidden
                    />
                    <div class="thumbnailLogo1">
                      <b-button
                        id="fileSelect"
                        @click="onPickFile()"                        
                        class="camCls"
                      >
                      </b-button>
                      <input
                        id="fileupload"
                        ref="fileupload"
                        @change="onFileChange"
                        type="file"
                        name="fileupload"
                        hidden
                      />
                    </div>
                  </div>
                  <div id="thumbnailLogoClient" v-else >
                    <img
                      @click="onPickFile()"
                      src="@/assets/browse_image.svg"
                      class="thumbnailLogo"
                      alt="client_icon"
                    />
                    <input
                      id="fileupload"
                      ref="fileupload"
                      @change="onFileChange"
                      type="file"
                      name="fileupload"
                      hidden
                    />
                    <div class="thumbnailLogo1">
                      <b-button
                        id="fileSelect"
                        @click="onPickFile()"                        
                        class="camCls"
                      >
                      </b-button>
                      <input
                        id="fileupload"
                        ref="fileupload"
                        @change="onFileChange"
                        type="file"
                        name="fileupload"
                        hidden
                      />
                    </div>
                  </div>
                </b-col>

                <b-col cols="10" xl="12" lg="12">
                  <p class="text-danger">{{ fileErrorMessage }}</p>
                </b-col>
              </b-row>
              <b-row>
                <b-col cols="12" xl="3" lg="3" class="justifyMobile">
                  <b-link
                    @click="deleteFile"
                    href="javascript:;"
                    class="card-link float-left link textColorClass ml-3"
                  >
                    <u>{{ $t("DeleteIcon") }}</u>
                  </b-link>
                </b-col>
              </b-row>
              <br />
            </b-form>
          </b-tab>
        </b-tabs>
      </b-card>
    </div>
    <b-modal
      id="tree-modal1"
      ref="tree-modal1"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="py-0"
      size="md"
      footer-class="border-0"
      header-class="border-0 bg-white pd-0"
      button-size="sm"
      hide-header-close
    >
      <template v-slot:modal-header="{ cancel }">
        <div class="title-fs font-weight-bold mt-2 text-dark ml-3">
          {{ $t("INDUSTRYSELECTION") }}
        </div>

        <b-button
          @click="cancel"
          class="back-btn close-btn mt-2"
          variant="primary"
          >{{ $t("CLOSE") }}</b-button
        >
      </template>
      <b-card class="pop-up-box">
        <b-row>
          <b-col>
            <SubMenuTree
              :treeTitle="industryTitle"
              @update-expense-type="selectIndustryItem"
              :industry="true"
              :data="treeData"
              :tree-options="treeOptions"
            />
          </b-col>
        </b-row>
      </b-card>
      <b-row>
        <b-col cols="12" class="text-center mb-2">
          <b-button
            :disabled="currentSelectedIndustryItem == ''"
            @click="getIndustryName"
            class="float-right"
            variant="primary"
            >{{ $t("SELECTEXPENSEITEMS") }}</b-button
          >
        </b-col>
      </b-row>
    </b-modal>
  </div>
</template>

<script>
import {
  required,
  email,
  numeric,
  minLength,
} from "vuelidate/lib/validators";
import { Settings } from "luxon";
import SubMenuTree from "@/components/SubMenuTree";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { mapGetters } from "vuex";
import ShowErrors from "@/components/ShowErrors";
Settings.defaultLocale = "ja";
import { endPoint } from "@/shared/endPoint";
import { Config } from "@/shared/config";
export default {
  name: "Index",
  components: {
    ShowErrors,
    SubMenuTree,
  },
  data() {
    return {
      baseDOMAIN: Config.apiDomain,
      imagePath: "",
      industryOptions: [],
      industryTitle: "",
      onRowDisabled: false,
      tabIndex: 0,
      treeOptions: {},
      tableYear: "",
      companySizeName: "",
      allTreeData: [],
      treeData: [],
      text: "",
      file: "",
      fileName: "",
      files: [],
      fileErrorMessage: "",
      officeOptions: [],
      options: [],
      monthOptions: [
        { item: "1", name: "1" },
        { item: "2", name: "2" },
        { item: "3", name: "3" },
        { item: "4", name: "4" },
        { item: "5", name: "5" },
        { item: "6", name: "6" },
        { item: "7", name: "7" },
        { item: "8", name: "8" },
        { item: "9", name: "9" },
        { item: "10", name: "10" },
        { item: "11", name: "11" },
        { item: "12", name: "12" },
      ],
      selectedOffice: "1",
      selectedYear: new Date().getFullYear(),
      clientOtherInfoNo: "",
      selectedMonth: new Date().getMonth() + 1,
      isAddSelected: true,
      isRowSelected: false,
      tableData: [],
      selectModeTable: "single",
      selectedRow: [],
      basicSettingInfo: {
        companyName: "",
        personInCharge: "",
        email: "",
        officeAddress: "",
        phoneNo: "",
      },

      companySettingInfo: {
        employee: "",
        capital: "",
        sales: "",
        floorSize: "",
        storeNo: "",
        industry: "",
        selectedIndustry: "",
      },
      currentMonth: new Date().getMonth() + 1,
      autoProposal: "",
      autoProposalDate: "",
      cntBaseInfoDate: "",
      companySizeInfoData: [],
      companyIndustryNo: "",
      isBusy: false,
      addBusy: false,
      updBusy: false,
      errorMessage: "",
      currentSelectedIndustryItem: "",
    };
  },
  computed: {
    ...mapGetters({
      selected_industry_item: "getSelectedIndustryItem",
    }),
    fieldss() {
      return [
        {
          key: "selectRow",
          label: this.$t("SELECT"),
          class: "text-center normal-width",
        },
        {
          key: "Year",
          label: this.$t("YEAR1"),
          class: "text-center normal-width",
        },
        {
          key: "EmployeeNumber",
          label: this.$t("EmployeeNumber"),
          class: "text-center normal-width",
        },
        {
          key: "Capital",
          label: this.$t("Capital1"),
          class: "text-center normal-width",
        },
        {
          key: "Revenue",
          label: this.$t("Revenue1"),
          class: "text-center normal-width",
        },

        {
          key: "Floorspace",
          label: this.$t("FloorSpace2"),
          class: "text-center normal-width",
        },
        {
          key: "NumberOfStores",
          label: this.$t("NumberOfStores"),
          class: "text-center normal-width",
        },
        {
          key: "IndustryType",
          label: this.$t("Industry1"),
          class: "text-center normal-width",
        },
      ];
    },
  },
  validations: {
