      this.companySettingInfo.employee = data ? data + this.$t("PEOPLE") : ""; //.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    commaSeparatedFloorSize(newVal) {
      console.log(newVal, "val");
      let data = this.commaSeparater(newVal);
      this.companySettingInfo.floorSize = data ? data + this.$t("PING") : ""; //.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    commaSeparatedStoreNo(newVal) {
      console.log(newVal, "val");
      let data = this.commaSeparater(newVal);
      this.companySettingInfo.storeNo = data ? data + this.$t("STOREUNIT") : ""; //.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    commaSeparatedRevenue(newVal) {
      let data = this.commaSeparater(newVal);
      this.companySettingInfo.sales = data ? data + this.$t("YEN") : ""; //newVal.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    commaSeparater(val) {
      return val.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    /**Function to compile data for selected row */
    toggleSelectAll(item) {
      this.$refs.selectableTable.selectRow(item.index);
      item.item.selectRow = !item.item.selectRow;
