      // this.refreshStore();
    },
    /**
     * @description function to fetch client details of logged in account
     */
    async getClientData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMCLIENTBASEINFO,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "client_no",
              value: localStorage.getItem("client_account_no"),
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        this.basicSettingInfo.companyName = responseData[0].client_name;
        this.basicSettingInfo.personInCharge =
          responseData[0].client_person_name;
        this.basicSettingInfo.phoneNo = responseData[0].client_tel;
        this.basicSettingInfo.email = responseData[0].client_mail;
        this.selectedOffice = responseData[0].client_main_office_kbn;
        this.basicSettingInfo.officeAddress =
          responseData[0].client_main_office_addr;
        this.cntBaseInfoDate = responseData[0].create_dt;
      }
    },
    /**
     * @description function to fetch the financial start month of logged in client account
     */
    async getStartMonthData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMCLIENTOTHERINFO,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "client_no",
              value: localStorage.getItem("client_account_no"),
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        console.log("responseData", responseData);
        let sortData = this._.sortBy(responseData, "client_other_info_no");
        this.clientOtherInfoNo = sortData[0].client_other_info_no;
        this.selectedMonth = sortData[0].year_start_month;
        this.autoProposal = sortData[0].auto_proposal;
        this.autoProposalDate = sortData[0].auto_proposal_dt;
        if (sortData[0].icon_file_name != "null") {
          this.fileName = sortData[0].icon_file_name;
          this.imagePath = sortData[0].icon_path;
        } else {
          this.imagePath = "";
        }
        this.$store.dispatch("setUserDetails", {
          client_account_no: localStorage.getItem("client_account_no"),
          client_no: localStorage.getItem("client_no"),
          clientName: localStorage.getItem("clientName"),
          clientPersonName: localStorage.getItem("clientPersonName"),
          clientCompanyName: localStorage.getItem("clientCompanyName"),
          icon_path: sortData[0].icon_path ? sortData[0].icon_path : "",
        });
      }
    },
    /**
     * @description function to fetch available industry and updating to store
     * @param data with ["01", "02"]
     */
    async getTreeData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMINDUSTRY,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "industry_kbn",
              value: ["01", "02"],
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        this.allTreeData = responseData;
        if (this.selected_industry_item.industry_nm) {
          this.companySettingInfo.industry = this.selected_industry_item.industry_nm;
        } else {
          this.allTreeData.forEach((item) => {
            if (
              this.companyIndustryNo == item.industry_no &&
              item.industry_kbn == 2
            ) {
              this.companySettingInfo.industry = item.industry_nm;
            }
          });
        }
        let mapData = [];
        let industryData = {
          text: "",
          value: "",
          disabled: true,
        };
        this.industryOptions.push(industryData);
        let sortData = this._.sortBy(responseData, "industry_big_cd");
        let groupedData = this._.groupBy(sortData, "industry_big_cd");
        Object.keys(groupedData).forEach((key) => {
          let childrenArray = [];
          let parentData = [];
          groupedData[key].forEach((item) => {
            if (item.industry_kbn == 1) {
              let parentNode = item.industry_nm;
              parentData.push(parentNode);
            } else {
              let bindChildData = {
                text: item.industry_nm,
              };

              industryData = {
                text: item.industry_nm,
                value: item.industry_no,
              };
              this.industryOptions.push(industryData);

              childrenArray.push(bindChildData);
            }
          });
          let name = ":" + parentData[0];
          if (key == "A") {
            let bindTreeData = {
              text: key + name,
              state: { expanded: true },
              children: childrenArray,
            };
            mapData.push(bindTreeData);
          } else {
            let bindTreeData = {
              text: key + name,
              state: { expanded: false },
              children: childrenArray,
            };
            mapData.push(bindTreeData);
          }
        });

        this.$store.dispatch("updateIndustryTree", mapData);
      }
    },
    /**
     * @description function to  select industry data
     * @name selectIndustryItem
     */
    selectIndustryItem(item) {
      this.currentSelectedIndustryItem = item;
    },
    /**
     * @description function to update selected industry and save to store
     */
    updateIndustryItem(item) {
      let Kbn2Data = [];
      this.allTreeData.forEach((item1) => {
        if (item1.industry_kbn == 2) {
          Kbn2Data.push(item1);
        }
      });
      let treeItem = this._.find(Kbn2Data, { industry_nm: item });
      this.$store.dispatch("updateselectedIndustryItem", {
        industry_nm: treeItem.industry_nm,
        industry_no: treeItem.industry_no,
      });
    },
    /**
     * @description function to fetch selected industry name and assign in UI field variables
     */
    getIndustryName() {
      this.updateIndustryItem(this.currentSelectedIndustryItem);
      if (this.selected_industry_item.industry_nm) {
        this.companySettingInfo.industry = this.selected_industry_item.industry_nm;
      }
      this.$refs["tree-modal1"].hide();
    },
    /**
     * @description function to fetch available prefectures data
     * @returns assign to dropDown fields
     */
    async prefectureData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMGENERALPURPOSE,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "key",
              value: "PREF",
            },
          ],
        }
      );
      let responseData = data.data;
      let codevalue = [];
      let value = [];
      let optionData = [];
      responseData.forEach((item) => {
        codevalue.push(parseInt(item.code));
        value = item.value_string_small;
        let i = 1;
        let insert = 0;
        if (codevalue[insert] == i) {
          optionData.push(value);
          i = i + 1;
        }
      });

      for (let index = 0; index < optionData.length; index++) {
        this.officeOptions.push({
          item: codevalue[index],
          name: optionData[index],
        });
      }
      let startyear = parseInt(new Date().getFullYear()) - 10;
      let endYear = parseInt(new Date().getFullYear()) + 10;
      for (let index = startyear; index <= endYear; index++) {
        this.options.push({ item: index, name: index + this.$t("YEAR") });
      }
    },
    /**
     * @description function to map UI fields based on selected rowData from table
     * @returns formData
     */
    onRowSelected(items) {
      if (items.length > 0) {
        this.onRowDisabled = true;
        this.isRowSelected = true;
        this.isAddSelected = false;
        let year = items[0].Year.slice(0, -1);
        let yeardata = parseInt(year);
        this.yearInfo = yeardata;
        this.selectedYear = yeardata;
        this.companySettingInfo.employee = items[0].EmployeeNumber.slice(0, -1);
        this.companySettingInfo.capital = items[0].Capital.slice(1);
        if (items[0].Revenue == null) {
          this.companySettingInfo.sales = null;
        } else {
          this.companySettingInfo.sales = items[0].Revenue.slice(1);
        }
        if (items[0].Floorspace == null) {
          this.companySettingInfo.floorSize = null;
        } else {
          this.companySettingInfo.floorSize = items[0].Floorspace;
        }
        this.companySettingInfo.storeNo = items[0].NumberOfStores;
        this.tableYear = year;
        if (items[0].IndustryType == this.$t("Big")) {
          //
          this.companySizeName = this.$t("BigIndustry");
        } else if (items[0].IndustryType == this.$t("Medium")) {
          this.companySizeName = this.$t("MediumIndustry");
        } else if (items[0].IndustryType == this.$t("Small")) {
          this.companySizeName = this.$t("SmallIndustry");
        } else if (items[0].IndustryType == "") {
          this.companySizeName = "";
        }
        let industryNo;
        this.companySizeInfoData.forEach((item) => {
          if (item.info_year == yeardata) {
            industryNo = item.industry_no;
          }
        });
        let kbnFlag = false;
        if (industryNo != null) {
          this.companySettingInfo.selectedIndustry = industryNo;
          this.allTreeData.forEach((item) => {
            if (item.industry_no == industryNo && item.industry_kbn == 2) {
              this.companySettingInfo.industry = item.industry_nm;
              kbnFlag = true;
            }
          });
        } else {
          this.companySettingInfo.industry = "";
        }
        if (kbnFlag == false) {
          this.companySettingInfo.industry = "";
        }
      } else {
        this.onRowDisabled = false;
        this.clearbasicSettingInfo();
      }
    },
    /**
     * @description function to remove japanese character from field value
     * @param {any} string
     * @param {any} value
     * @returns string value
     */
    removeChar(value) {
      try {
        return value.substring(1).replace(/[^0-9.-]+/g, "");
      } catch (error) {
        return 0;
      }
    },
    /**
     * @description function to convert japanese currency format to number
     * @param {any} int
     * @param {any} value
     * @returns int value
     */
    NumberConvert(value) {
      try {
        return value.replace(/[^0-9.-]+/g, "");
      } catch (error) {
        console.log(error);
      }
    },
    /**
     * @description function to convert number format to japanese currency format
     * @param {any} int
     * @param {any} value
     * @returns string currency value
     */
    filterByCurrency(value) {
      try {
        return parseInt(value)
          .toString()
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
      } catch {
        return 0;
      }
    },
    /**
     * @description function to fetch getCompanySizeInfoTableData from API
     * @returns tableData
     */
    async getCompanySizeInfoTableData() {
      this.isBusy = true;
      let type1Data = [];

      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMCLIENTCOMPANYSIZEINFO,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "client_no",
              value: localStorage.getItem("client_account_no"),
            },
          ],
        }
      );
      this.isBusy = false;
      console.log(localStorage.getItem("client_account_no"));
      let responseData = data.data;
      this.companySizeInfoData = responseData;
      if (this.companySizeInfoData.length > 0) {
        this.errorMessage = "";
        let responseData = data.data;
        this.companySizeInfoData = responseData;
        let sortedData = this._.sortBy(responseData, "info_year").reverse();
        sortedData.forEach((item) => {
          if (item.company_size == 1) {
            //
            this.companytype = this.$t("Small");
          } else if (item.company_size == 2) {
            this.companytype = this.$t("Medium");
          } else if (item.company_size == 3) {
            this.companytype = this.$t("Big");
          } else {
            this.companytype = "";
          }
          let revenue;
          let floorSpace;
          if (item.revenue == null && item.floor_space == null) {
            revenue = null;
            floorSpace = null;
          } else if (item.revenue != null && item.floor_space == null) {
            revenue =
              this.$t("JapaneesCurrency") + this.filterByCurrency(item.revenue);
            floorSpace = null;
          } else if (item.revenue == null && item.floor_space != null) {
            revenue = null;
            floorSpace = this.filterByCurrency(item.floor_space);
          } else {
            revenue =
              this.$t("JapaneesCurrency") + this.filterByCurrency(item.revenue);

            floorSpace = this.filterByCurrency(item.floor_space);
          }

          let tableinfo = {
            Year: item.info_year + this.$t("YEAR"),
            EmployeeNumber: item.employee_number + this.$t("PEOPLE"),
            Capital:
              this.$t("JapaneesCurrency") + this.filterByCurrency(item.capital),
            Revenue: revenue,
            Floorspace: floorSpace,
            NumberOfStores: item.number_of_stores,
            IndustryType: this.companytype,
          };
          type1Data.push(tableinfo);
        });

        this.tableData = type1Data;
        this.tableYear = this.tableData[0].Year.slice(0, -1);
        if (this.tableData[0].IndustryType == this.$t("Big")) {
          //
          this.companySizeName = this.$t("BigIndustry");
        } else if (this.tableData[0].IndustryType == this.$t("Medium")) {
          this.companySizeName = this.$t("MediumIndustry");
        } else if (this.tableData[0].IndustryType == this.$t("Small")) {
          this.companySizeName = this.$t("SmallIndustry");
        } else if (this.tableData[0].IndustryType == "") {
          this.companySizeName = "";
        }
        this.companyIndustryNo = sortedData[0].industry_no;
        this.getTreeData();
      } else {
        this.isBusy = false;
        this.errorMessage = this.$t("GENRALDATAERRORMESSAGE");
        this.getTreeData();
      }
    },
    /**
     * @description function to display modal popup
     */
    addIndustryNo() {
      this.currentSelectedIndustryItem = "";
      this.$refs["tree-modal1"].show();
    },
    /**
     * @description function to check whether client basic information data to update or Create
     * After checking, calls respective API(update or create)
     */
    clientBasicInfoCheck() {
      this.$v.basicSettingInfo.$touch();
      if (!this.$v.basicSettingInfo.$invalid) {
        let self = this;
        this.$swal({
          text: this.$t("InsertMessage"),
          showCancelButton: true,
          confirmButtonText: "OK",
          cancelButtonText: this.$t("CANCEL"),
          cancelButtonColor: "#062854",
          cancelButtonClass: "btn-primary",
        }).then(async function (isconfirm) {
          if (!isconfirm.value == true) {
            return;
          } else {
            const { data } = await BasicInfoApis.postAPI(
              endPoint.MASTERREADDATAMCLIENTBASEINFO,
              {
                fieldlist: ["*"],
                conditions: [
                  {
                    key: "client_no",
                    value: localStorage.getItem("client_account_no"),
                  },
                ],
              }
            );
            if (data.success) {
              let responseData = data.data;
              if (responseData.length > 0) {
                self.callUpdateAPI();
              } else {
                self.callCreateAPI();
              }
            }
          }
        });
      }
    },
