    /**
     * @description function to check and validate browsed files
     * @param {file} data
     */
    onFileChange(event) {
      let fileSize = 0;
      for (var i = 0; i < event.target.files.length; i++) {
        var ext = event.target.files[i].name.split(".").pop();
        fileSize += Math.round(event.target.files[i].size / 1024);
        if (
          ext == "jpg" ||
          ext == "png" ||
          ext == "jpeg" ||
          ext == "JPG" ||
          ext == "PNG" ||
          ext == "JPEG"
        ) {
          if (fileSize > 1024) {
            this.fileErrorMessage = this.$t("MAXICONSIZE");
          } else {
            for (let i = 0; i < event.target.files.length; i++) {
              this.files.push(event.target.files[i]);
            }
            this.fileName = event.target.files[0].name;
            this.fileupload();
          }
        } else {
          this.fileErrorMessage = this.$t("UPLOADEXCECUTABLEFILES");
        }
        fileSize += Math.round(event.target.files[i].size / 1024);
      }
    },
    /**
     * @description function to upload validated browsed files
     * @param {formData} data
     */
    async fileupload() {
      this.fileErrorMessage = "";
      var formData = new FormData();
      for (let i = 0; i < this.files.length; i++) {
        formData.append("attachments[]", this.files[i]);
      }

      formData.append("client_no", localStorage.getItem("client_account_no"));
      formData.append("update_id", localStorage.getItem("client_account_no"));
      formData.append(
        "update_dt",
        new Date().getFullYear() +
          "-" +
          this.currentMonth +
          "-" +
          new Date().getDate()
      );
      formData.append("year_start_month", this.selectedMonth);

      formData.append(
